<template>
  <div class="container">
    <div style="width: 800px;">
      <!-- 欢迎语 -->
      <welcome
        :task_list_undone_count="task_list_count"
        :task_list_length="task_list.length"
      />

      <!-- 添加待办事项 -->
      <div>
        <el-input
          placeholder="快速添加今日待办事项"
          prefix-icon="el-icon-plus"
          v-model="new_task.title"
          class="input-new-task"
          @keyup.enter.native="add_task"
        >
          <el-select
            v-model="new_task.checklist_id"
            slot="prepend"
            placeholder="请选择"
            style="width: 150px;"
          >
            <el-option
              v-for="(item, index) in checklist"
              :key="index"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-input>
      </div>

      <div v-if="task_list.length">
        <!-- 待办事项卡片 -->
        <el-card
          class="box-card"
          shadow="hover"
          v-for="(item, index) in task_list_sort"
          :key="index"
        >
          <el-row :gutter="10">
            <el-col :span="1">
              <!-- 左侧图标 -->
              <div class="task-icon" @click="change_task_status(item)">
                <span v-if="item.status">
                  <i class="el-icon-check" />
                </span>
                <span v-else>
                  <i class="el-icon-s-flag" id="live2d-flag" />
                </span>
              </div>
            </el-col>

            <!-- 待办事项标题 -->
            <el-col :span="item.task_time ? 15 : 20">
              <div :class="{ task_finished: item.status }">
                {{ item.title }}
              </div>
            </el-col>

            <!-- 待办事项时间 -->
            <el-col :span="item.task_time ? 5 : 0">
              <div class="task-time">
                {{ item.task_time }}
              </div>
            </el-col>

            <!-- 待办事项操作 -->
            <el-col :span="3">
              <div class="box-card__tool box-card__show">
                <i class="el-icon-more" @click="edit_task(item, index)" />
                <i
                  class="el-icon-document-copy"
                  @click="clip_task(item.title)"
                />
                <i class="el-icon-delete" @click="delete_task(item)" />
              </div>
            </el-col>
          </el-row>
        </el-card>
      </div>
      <div v-else class="no-task">
        <i class="el-icon-info" />
        还没有待办事项呢
      </div>

      <!-- 待办事项信息拟态框 -->
      <task-dialog
        :dialog="dialog"
        @dialogchange="dialogchange"
        :task-info="target_task"
        @taskinfochange="taskinfochange"
      />
    </div>
  </div>
</template>

<script>
import { addTask, changeTask, deleteTask, getTask } from "@/api/task.js";
import { checklist_list_get } from "@/api/checklist";
import Welcome from "./components/Welcome";
import taskDialog from "@/components/taskDialog";

export default {
  components: {
    Welcome,
    taskDialog
  },
  data() {
    return {
      new_task: {
        title: "",
        content: "",
        cycle: "",
        checklist_id: "1"
      },
      task_list: [],
      dialog: false,
      dialog_status: true,
      target_task_index: 0,
      target_task: {
        title: "",
        content: "",
        cycle: "0",
        task_time: ""
      },
      checklist: []
    };
  },
  methods: {
    fetch_data() {
      let data = {
        date: this.$moment().format("Y-MM-DD")
      };
      getTask(this.$store.getters.token, data).then(response => {
        this.task_list = response.data;
      });
      checklist_list_get(this.$store.getters.token).then(response => {
        this.checklist = response.data;
        this.new_task.checklist_id = response.default_checklist_id;
      });
    },
    add_task() {
      if (!this.new_task.title) {
        this.$notify({
          title: "系统通知",
          message: "待办事项名不可为空",
          type: "warning"
        });
        return;
      }

      let data = {
        title: this.new_task.title,
        content: this.new_task.content,
        add_time: this.$moment().format("Y-MM-DD"),
        checklist_id: this.new_task.checklist_id
      };
      addTask(this.$store.getters.token, data).then(() => {
        this.fetch_data();
        this.$notify({
          title: "系统通知",
          message: "添加待办事项成功",
          type: "success"
        });
        Object.keys(this.new_task).forEach(key => (this.new_task[key] = ""));
      });
    },
    clip_task(text) {
      this.$copyText(text).then(() => {
        this.$notify({
          title: "系统通知",
          message: "复制待办事项成功",
          type: "success"
        });
      });
    },
    delete_task(task) {
      deleteTask(this.$store.getters.token, task.task_id).then(() => {
        for (let i = 0; i < this.task_list.length; i++) {
          if (this.task_list[i] === task) {
            this.task_list.splice(i, 1);
            this.$notify({
              title: "系统通知",
              message: "删除待办事项成功",
              type: "success"
            });
          }
        }
      });
    },
    change_task(task) {
      let data = {
        title: task.title,
        content: task.content,
        status: task.status,
        cycle: task.cycle,
        task_time: task.task_time
      };
      changeTask(this.$store.getters.token, task.task_id, data);
    },
    edit_task(task, index) {
      this.target_task = JSON.parse(JSON.stringify(task));
      this.target_task_index = index;
      this.dialog = true;
      this.dialog_status = false;
    },
    change_task_status(task) {
      this.task_list.forEach(item => {
        if (item === task) item.status = !item.status;
      });
      this.change_task(task);
      if (task.status) {
        this.$notify({
          title: "系统通知",
          message: "完成待办事项成功",
          type: "success"
        });
      } else {
        this.$notify({
          title: "系统通知",
          message: "取消待办事项完成",
          type: "success"
        });
      }
    },
    dialogchange(val) {
      this.dialog = val;

      this.target_task = {
        title: "",
        content: "",
        task_time: "",
        cycle: "0"
      };
    },
    taskinfochange(val) {
      this.task_list[this.target_task_index] = val;
      if (!this.dialog_status) {
        // 后端修改信息
        changeTask(this.$store.getters.token, val.task_id, val).then(() => {
          this.dialog_status = true;
          this.$notify({
            title: "系统通知",
            message: "修改成功",
            type: "success"
          });
        });
      } else {
        addTask(this.$store.getters.token, val).then(() => {
          this.$notify({
            title: "系统通知",
            message: "添加成功",
            type: "success"
          });
        });
      }
    }
  },
  created() {
    this.fetch_data();
  },
  computed: {
    task_list_sort() {
      // eslint-disable-next-line vue/no-side-effects-in-computed-properties
      return this.task_list.sort((m, n) => {
        if (m.status !== n.status) {
          return m.status - n.status;
        } else {
          return m.task_id - n.task_id;
        }
      });
    },
    task_list_count() {
      let new_task_list = this.task_list.filter(item => {
        return !item.status;
      });
      return new_task_list.length;
    }
  }
};
</script>

<style scoped>
.container {
  padding: 100px;
  display: flex;
  justify-content: center;
}

.input-new-task {
  margin: 20px 0;
}

.new-task__container {
  display: flex;
  align-items: center;
}

.new-task__tool {
  display: flex;
  justify-content: center;
}

.box-card {
  margin-bottom: 10px;
  min-width: 750px;
}

.box-card__tool {
  display: flex;
  justify-content: space-around;
}

.task-icon,
.box-card__tool i:hover {
  cursor: pointer;
}

.box-card__show {
  display: none;
}

.box-card:hover .box-card__show {
  display: flex;
}

.task_finished {
  text-decoration: line-through;
}
</style>
